import { all } from 'redux-saga/effects';

import { watchAuth } from './auth';
import { watchCardChange } from './card';
import { watchConnectionStatusChange } from './connection';
import { watchAddRoadmap, watchCurrentRoadmapChange } from './roadmap';

export default function* rootSaga(): Generator<unknown, void, unknown> {
  yield all([
    watchAuth(),
    watchCardChange(),
    watchConnectionStatusChange(),
    watchAddRoadmap(),
    watchCurrentRoadmapChange(),
  ]);
}
