import { CSSProperties } from 'react';

import C from 'constants/index';

import { placeholderInitState } from './placeholderActions';
import { getDaysDiff, getDaysInterval } from './dates';

import { PlaceholderData } from 'types/index';
import { Card } from 'types/state';
import { CardTypes } from 'types/card';

export const getColumnSize = (): number => Math.ceil(window.innerWidth / C.DAY.WIDTH);

export const getPlaceholderWidth = (x1: number, x2: number): number =>
  Math.max(Math.round((x2 - x1) / C.DAY.WIDTH + 0.3), 1) || 1;

export const getPlaceholderData = (x1: number, x2: number, y1: number, shouldSkipAim: boolean): PlaceholderData => {
  if (shouldSkipAim || (x1 === 0 && y1 === 0)) {
    return {
      style: { opacity: '0' },
    };
  }
  if (x2 === 0) {
    return {
      style: { transform: `translate(${x1}px, ${y1}px)`, opacity: '1' },
    };
  }
  const size = getPlaceholderWidth(x1, x2);
  return {
    style: {
      transform: `translate(${x1}px, ${y1}px)`,
      width: String((size - 1) * C.DAY.WIDTH) + 'px',
    },
    size,
  };
};

export const getProgressBackgroud = (
  complete?: number,
  color1: string = '#f50',
  color2: string = '#ddd'
): CSSProperties | undefined =>
  typeof complete === 'number'
    ? { background: `linear-gradient(to right, ${color1} ${complete}%, ${color2} ${complete}%)` }
    : undefined;

export const isAimOnCard = (aim: typeof placeholderInitState, cards: Array<Card>): boolean => {
  const shortList = cards.filter(
    (card) =>
      card.line === aim.line && card.from.localeCompare(aim.date1) <= 0 && card.until.localeCompare(aim.date1) >= 0
  );
  if (shortList.some((card) => card.from === aim.date1 || card.until === aim.date1)) {
    return true;
  }
  return Boolean(shortList.length);
};

export const getCardStatus = (from: string, until: string, today: string, progress?: number): CardTypes | undefined => {
  // No progress => No status
  if (progress === undefined) {
    return;
  }
  // 'pending':  card is in the future or starting today and progress is zero
  // 'progress': card is in the future and progress > zero || progress >= proportion of today in the timeframe
  // 'delayed':  progress < proportion of today in the timeframe
  // 'complete': progress is 100
  // 'dropped':  card is in the past and progress is zero
  if (progress === 100) {
    return 'complete';
  }
  // Today is after Until (Card is in the past)
  if (today.localeCompare(until) > 0) {
    return 'dropped';
  }
  if (progress === 0) {
    // From is before Today (Card from past without progress)
    if (today.localeCompare(from) > 0) {
      return 'delayed';
    }
    // From is not yet reached or Today (Card is in the plan)
    if (today.localeCompare(from) <= 0) {
      return 'pending';
    }
  }
  // If progress is between 0 and 100 it's either in progress or delayed
  const fromToToday = getDaysDiff(from, today);
  const todayToUntilPlusOne = getDaysInterval(today, until);
  const proportion = fromToToday / (fromToToday + todayToUntilPlusOne);
  if (progress / 100 < proportion) {
    return 'delayed';
  } else {
    return 'progress';
  }
};

export const autoscaleTextarea = (textarea: HTMLTextAreaElement, setFocus: boolean = false): void => {
  textarea.style.height = 'inherit';
  textarea.style.height = `${textarea.scrollHeight}px`;
  if (setFocus && Number.isInteger(textarea.selectionStart)) {
    textarea.selectionStart = textarea.value.length;
    textarea.selectionEnd = textarea.value.length;
  }
};

export const hasCardChanged = (edited: Card, original?: Card): boolean =>
  !!original && (Object.keys(edited) as [keyof Card]).every((key) => original[key] === edited[key]);

export const isCardValid = (selection: Card): boolean =>
  selection.id !== '' &&
  selection.line >= 0 &&
  selection.text !== '' &&
  (selection.progress === undefined ||
    (Number.isInteger(selection.progress) && selection.progress >= 0 && selection.progress <= 100)) &&
  selection.from.length === 10 &&
  selection.until.length === 10 &&
  getDaysDiff(selection.from, selection.until) >= 0;

export const scrollToToday = (list: HTMLUListElement, today: HTMLLIElement, smooth?: boolean): void =>
  list.scroll({
    left: today.offsetLeft - window.innerWidth / 2,
    behavior: smooth ? 'smooth' : 'auto',
  });
