import C from 'constants/index';

import { DateType, FullDateType } from 'types';
import { MoveCard } from 'types/actions';

export const getTwoDigitDate = (number: number): string => `0${number}`.slice(-2);
// export const getDataString = (date: DateType): string => `${date.year}-${date.month}-${date.day}`;

export const getYMDFromTimestamp = (timestamp: number): DateType => {
  const date = new Date(timestamp);
  return {
    year: date.getUTCFullYear(),
    month: date.getUTCMonth() + 1,
    day: date.getUTCDate(),
  };
};

export const getWeekNumber = (timestamp: number): number => {
  const date = new Date(timestamp);
  date.setUTCDate(date.getUTCDate() + 4 - (date.getUTCDay() || 7));
  const yearStart = new Date(Date.UTC(date.getUTCFullYear(), 0, 1));
  const dateDiff = date.getTime() - yearStart.getTime();
  return Math.ceil((dateDiff / C.MILLISECONDS_IN_DAY + 1) / 7);
};
export const getWeekdayFromTimestamp = (timestamp: number, long: boolean = true): string => {
  return new Date(timestamp).toLocaleDateString(C.LOCALE, { weekday: long ? 'long' : 'narrow' });
};

export const getMonthNameFromTimestamp = (timestamp: number): string => {
  return new Date(timestamp).toLocaleDateString(C.LOCALE, { month: 'long' });
};

// For now we assume weekends are only Saturdays & Sundays
export const isWeekend = (timestamp: number): boolean => new Date(timestamp).getDay() % 6 === 0;

export const getTodayTimestamp = (): number => {
  const today = new Date();
  return Date.UTC(today.getFullYear(), today.getMonth(), today.getDate());
};

export const getDataString = (timestamp: number = getTodayTimestamp()): string =>
  new Date(timestamp).toISOString().slice(0, 10);

export const getLocalString = (timestamp: number = getTodayTimestamp()): string => new Date(timestamp).toLocaleString();

export const getFullDateFromTimestamp = (timestamp: number, isToday: boolean = false): FullDateType => ({
  date: getYMDFromTimestamp(timestamp),
  dateString: getDataString(timestamp),
  monthName: getMonthNameFromTimestamp(timestamp),
  weekDayName: getWeekdayFromTimestamp(timestamp),
  weekDayShort: getWeekdayFromTimestamp(timestamp, false),
  weekNumber: getWeekNumber(timestamp),
  isWeekend: isWeekend(timestamp),
  isToday,
});

export const getDatesArray = (columnSize: number, todayTimestamp: number): FullDateType[] =>
  Array(columnSize)
    .fill(todayTimestamp)
    .map((dateString: number, index: number) => {
      const timestamp = dateString + (index - columnSize / 2) * C.MILLISECONDS_IN_DAY;
      return getFullDateFromTimestamp(timestamp, todayTimestamp === timestamp);
    });

export const extendDatesArray = (datesArray: FullDateType[], extraDays: number, backward?: boolean): FullDateType[] => {
  const lastDay = datesArray[backward ? 0 : datesArray.length - 1];
  const lastTimestamp = Date.UTC(lastDay.date.year, lastDay.date.month - 1, lastDay.date.day);
  const extraArray = Array(extraDays)
    .fill(lastTimestamp)
    .map((dateString: number, index: number) => {
      const timestamp = dateString + (backward ? index - extraDays : index + 1) * C.MILLISECONDS_IN_DAY;
      return getFullDateFromTimestamp(timestamp, false);
    });
  return backward ? [...extraArray, ...datesArray] : [...datesArray, ...extraArray];
};

export const getDaysDiff = (date1: string, date2: string): number =>
  (new Date(date2).getTime() - new Date(date1).getTime()) / C.MILLISECONDS_IN_DAY;

export const getDaysInterval = (date1: string, date2: string): number => getDaysDiff(date1, date2) + 1;

export const getDateFromDiff = (date: string, diff: number): string =>
  getDataString(new Date(date).getTime() + diff * C.MILLISECONDS_IN_DAY);

export const getDaysInMonth = (year: number, month: number): number => new Date(year, month, 0).getDate();

export const getMilisecondsUntilTomorrow = (days: number = 1): number => {
  const today = new Date();
  const otherDay = new Date(today.getFullYear(), today.getMonth(), today.getDate() + days);
  return otherDay.getTime() - today.getTime();
};

export const getNewDates = ({ direction, dateDiff }: MoveCard['payload'], dates: string[]): string[] => {
  switch (direction) {
    case -1:
      return [getDateFromDiff(dates[0], dateDiff), dates[1]];
    case 1:
      return [dates[0], getDateFromDiff(dates[1], dateDiff)];
    default:
      return dates.map((date) => getDateFromDiff(date, dateDiff));
  }
};
