import styled, { keyframes } from 'styled-components';

import color from 'constants/colors';

export const rotate = keyframes`
  to {
    transform: rotate(360deg);
  }
`;

export const Spinner = styled.div`
  width: ${({ size = 50 }: { size?: number }) => `${size}px`};
  height: ${({ size = 50 }: { size?: number }) => (size ? `${size}px` : '50px')};
  margin: ${({ size }: { size?: number }) => (size ? '3px' : 'auto')};
  border: ${({ size = 50 }: { size?: number }) => `${Math.ceil(size / 10)}px solid transparent`};
  border-top-color: ${({ size }: { size?: number }) => (size ? color.header.acid : 'rgba(106, 148, 184, 0.5)')};
  border-radius: 50%;
  animation: ${rotate} 1s linear infinite;
`;
