import styled from 'styled-components';

import { DayHeaderDayNumber } from './DayHeader';

export const DateTableBlock = styled.ul`
  margin: 0;
  padding: 0;
  font-size: 0;
  overflow: scroll;
  white-space: nowrap;
  position: relative;
  user-select: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    display: none;
  }
`;

export const DateColumnItem = styled.li`
  list-style: none;
  width: 30px;
  height: 200%;
  display: inline-block;
  box-shadow: inset 1px 0 0 rgba(0, 0, 0, 0.06);
  font-size: 12px;
  vertical-align: top;
  &.today {
    background: linear-gradient(#f00, #8c0756) no-repeat center/2px 100%;
    & ${DayHeaderDayNumber} {
      border-radius: 50%;
      color: #fff;
      background: #f00;
      font-weight: bolder;
      padding: 2px;
      margin: 6px;
    }
  }
  &.weekend {
    background-color: rgba(106, 148, 184, 0.06);
  }
  &.month {
    box-shadow: inset 1px 0 0 rgba(0, 0, 0, 0.2);
  }
`;

export const TodayScroll = styled.button`
  border: none;
  font-size: 12px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background: rgb(219 228 236);
  box-shadow: 0 2px 6px -1px rgba(0, 0, 0, 0.2), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin: 3px auto;
  line-height: 24px;
  padding: 0 7px;
  -webkit-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
  cursor: pointer;
  z-index: 10;
  color: #555;
  user-select: none;
  opacity: ${({ scrollPosition }: { scrollPosition: number }) => (scrollPosition === 0 ? '0' : '1')};
  pointer-events: ${({ scrollPosition }: { scrollPosition: number }) => (scrollPosition === 0 ? 'none' : undefined)};
  &:hover {
    box-shadow: 0 3px 6px -1px rgba(0, 0, 0, 0.4), inset 0 0 0 1px rgba(0, 0, 0, 0.05);
  }
  &:before {
    content: ${({ scrollPosition }: { scrollPosition: number }) => scrollPosition < 0 && `"\\276E"`};
    margin-right: 4px;
  }
  &:after {
    content: ${({ scrollPosition }: { scrollPosition: number }) => scrollPosition > 0 && `"\\276F"`};
    margin-left: 4px;
  }
`;
