import {
  put,
  fork,
  take,
  call,
  select,
  race,
  TakeEffect,
  ForkEffect,
  SelectEffect,
  CallEffect,
  PutEffect,
} from 'redux-saga/effects';

import { getRoadmapContent, removeRemoteAccountRoadmap, addRemoteAccountRoadmap } from 'data/data';
import { removeAccountRoadmap, roadmapDataFetch, setAccountRoadmaps } from 'state/actions/user';
import { hydrateWithIDs } from 'utils/arrays';

import C from 'constants/index';

import { AddAccountRoadmap, SetAccountRoadmaps, SetCurrentRoadmap } from 'types/actions';
import { PersistentState, State } from 'types/state';

function* fetchCurrentRoadmapData(currentRoadmapID: string) {
  const channel = getRoadmapContent(currentRoadmapID);
  let isCancelled = false;
  try {
    while (true) {
      const { roadmapData } = (yield race({
        roadmapData: take(channel),
        cancel: take('REMOVE_CURRENT_ROADMAP'),
      })) as { roadmapData: PersistentState };
      if (roadmapData) {
        const hydratedData = hydrateWithIDs({
          cards: roadmapData.cards || {},
          milestones: roadmapData.milestones || {},
        });
        yield put(roadmapDataFetch(hydratedData));
      } else {
        isCancelled = true;
        channel.close();
      }
    }
  } finally {
    if (!isCancelled) {
      // Error in data fetch: requested roadmap doesn't exist. Removing its ID from user account.
      yield put(removeAccountRoadmap(currentRoadmapID));
      const userID = (yield select((state: State) => state.user.id)) as string;
      yield call(removeRemoteAccountRoadmap, userID, currentRoadmapID);
    }
  }
}

export function* watchCurrentRoadmapChange(): Generator<TakeEffect | ForkEffect<void>, void, SetCurrentRoadmap> {
  while (true) {
    const { currentRoadmapID } = (yield take(['SET_CURRENT_ROADMAP'])).payload;
    if (typeof currentRoadmapID === 'string' && currentRoadmapID.length === C.ID_LENGTH) {
      yield fork(fetchCurrentRoadmapData, currentRoadmapID);
    }
  }
}

export function* watchAddRoadmap(): Generator<
  TakeEffect | SelectEffect | CallEffect<string> | PutEffect<SetAccountRoadmaps> | ForkEffect<void>,
  void,
  AddAccountRoadmap | string
> {
  while (true) {
    const { roadmapName } = ((yield take(['ADD_ACCOUNT_ROADMAP'])) as AddAccountRoadmap).payload;
    if (typeof roadmapName === 'string' && roadmapName.length) {
      const userID = (yield select((state: State) => state.user.id)) as string;
      const createdAt = Date.now();
      const roadmapID = (yield call(addRemoteAccountRoadmap, userID, createdAt, roadmapName)) as string;
      yield put(
        setAccountRoadmaps(
          {
            [roadmapID]: true,
          },
          {
            [roadmapID]: {
              name: roadmapName,
              createdAt,
            },
          }
        )
      );
      // const movePage = (roadmapID: string) => history.push(`/roadmap/${roadmapID}/`);
      // yield fork(movePage, roadmapID);
    }
  }
}
