import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';

import { getTodayTimestamp, getMilisecondsUntilTomorrow, getDataString } from 'utils/dates';
import { selectionCancel } from 'state/actions/selection';
import { placeholderUpdate } from 'utils/placeholderActions';
import { TodayString } from 'context/dates';
import DateTable from 'components/DateTable/DateTable';

import { MainBlock } from 'styles/Main';

let timeout: number;

const Roadmap = (): JSX.Element => {
  const [todayTimestamp, setTodayTimestamp] = useState(getTodayTimestamp());
  const dispatch = useDispatch();
  useEffect(() => {
    const manageTimer = () => {
      if (document.hidden) {
        window.clearTimeout(timeout);
      } else {
        const newTimestamp = getTodayTimestamp();
        if (todayTimestamp !== newTimestamp) {
          setTodayTimestamp(getTodayTimestamp());
        } else {
          timeout = window.setTimeout(() => {
            setTodayTimestamp(getTodayTimestamp());
          }, getMilisecondsUntilTomorrow());
        }
      }
    };
    manageTimer();
    document.addEventListener('visibilitychange', manageTimer);
    return () => {
      document.removeEventListener('visibilitychange', manageTimer);
    };
  }, [todayTimestamp]);
  useEffect(() => {
    const cancelAction = (e: KeyboardEvent): void => {
      if (e.key.toLocaleLowerCase() === 'escape') {
        e.preventDefault();
        dispatch(selectionCancel());
        placeholderUpdate.update({
          started: false,
        });
      }
    };
    document.addEventListener('keydown', cancelAction);
    return () => {
      document.removeEventListener('keydown', cancelAction);
    };
  }, [dispatch]);

  return (
    <MainBlock>
      <TodayString.Provider value={getDataString(todayTimestamp)}>
        <DateTable todayTimestamp={todayTimestamp} />
      </TodayString.Provider>
    </MainBlock>
  );
};

export default React.memo(Roadmap);
