import React from 'react';
import { useSelector } from 'react-redux';
import CloudOff from '@material-ui/icons/CloudOff';
import CloudUpload from '@material-ui/icons/CloudUploadOutlined';
import CloudDone from '@material-ui/icons/CloudDoneOutlined';

import { AvatarBlock } from 'styles/Header';

import { State } from 'types/state';

const StatusIcon = {
  off: <CloudOff />,
  syncing: <CloudUpload />,
  on: <CloudDone />,
};

const ConnectionStatus = (): JSX.Element | null => {
  const user = useSelector((state: State) => state.user);
  const connectionStatus = useSelector((state: State) => state.ui.connectionStatus);
  return user.id ? <AvatarBlock>{StatusIcon[connectionStatus]}</AvatarBlock> : null;
};

export default React.memo(ConnectionStatus);
