import React from 'react';
import { useSelector } from 'react-redux';

import { handleAuth, handleAuthOut } from 'data/auth';

import { Spinner } from 'styles/Common/Spinner';
import { AvatarBlock, SignIn } from 'styles/Header';

import { State } from 'types/state';

const Avatar = (): JSX.Element => {
  const user = useSelector((state: State) => state.user);
  if (user.fetchig) {
    return (
      <AvatarBlock onClick={handleAuthOut}>
        <Spinner size={20} />
      </AvatarBlock>
    );
  }
  if (user.id !== '') {
    return (
      <AvatarBlock onClick={handleAuthOut}>
        {/* TODO: check if the image is renadable & replace image only when it's loaded */}
        {user.photoURL ? <img src={user.photoURL} alt={user.name} /> : null}
      </AvatarBlock>
    );
  }
  return <SignIn onClick={handleAuth}>Sign In</SignIn>;
};

export default React.memo(Avatar);
