import React from 'react';
import { useSelector } from 'react-redux';

import { handleAuth } from 'data/auth';
import { getLocalString } from 'utils/dates';

import C from 'constants/index';

import { Spinner } from 'styles/Common/Spinner';
import { Toast } from 'styles/Common/Toast';
import {
  RoadmapList,
  RoadmapListTitle,
  RoadmapListBubble,
  RoadmapListItem,
  RoadmapListAdd,
  RoadmapListItemDetail,
} from 'styles/Overview';

import { State } from 'types/state';

const limit = C.PLAN.FREE.LIMIT.ROADMAPS;

const Overview = ({ showInput }: { showInput: (value: boolean) => void }): JSX.Element => {
  const userRoadmapsSet = useSelector((state: State) => state.user.roadmapsSet);
  const userRoadmaps = useSelector((state: State) => state.user.roadmaps);
  const roadMeta = useSelector((state: State) => state.roadMeta);
  const user = useSelector((state: State) => state.user);
  if (!user.fetchig && !user.id) {
    return (
      <Toast>
        Please <button onClick={handleAuth}>sign in</button> to load existing or create new roadmaps.
      </Toast>
    );
  }
  if (!userRoadmapsSet) {
    return <Spinner />;
  }
  const roadmapsIDs = Object.keys(userRoadmaps);
  const createRoadmap = () => showInput(true);
  return (
    <RoadmapList>
      <RoadmapListTitle>
        Available Roadmaps
        <RoadmapListBubble title={`With free plan you can create up to ${limit} roadmaps`}>
          {roadmapsIDs.length}/{limit}
        </RoadmapListBubble>
      </RoadmapListTitle>
      {roadmapsIDs
        .sort((a, b) => roadMeta[a].createdAt - roadMeta[b].createdAt)
        .map(
          (id, index) =>
            index < limit && (
              <RoadmapListItem key={id} to={`/roadmap/${id}/`}>
                {roadMeta[id].name}
                <RoadmapListItemDetail>Creation date: {getLocalString(roadMeta[id].createdAt)}</RoadmapListItemDetail>
              </RoadmapListItem>
            )
        )}
      {roadmapsIDs.length < limit && <RoadmapListAdd onClick={createRoadmap}>Create new roadmap</RoadmapListAdd>}
    </RoadmapList>
  );
};

export default React.memo(Overview);
