import { put, select, take, actionChannel, delay, race, ActionPattern, ActionChannelEffect } from 'redux-saga/effects';
import { RaceEffect, TakeEffect, ChannelTakeEffect, PutEffect, SelectEffect, CallEffect } from 'redux-saga/effects';

import { getDataConnectionStatus } from 'data/connection';

import { Actions } from 'types/actions';
import { ConnectionStatus, State } from 'types/state';

type StatusChange = Generator<SelectEffect | PutEffect<Actions>, void, ConnectionStatus>;
type RaceEffects = RaceEffect<TakeEffect | ChannelTakeEffect<boolean>>;
type Effects = ActionChannelEffect | RaceEffects | PutEffect<Actions> | CallEffect<number>;

export function* setStatusChange(status: ConnectionStatus): StatusChange {
  const currentStatus = yield select((state: State) => state.ui.connectionStatus);
  if (status === 'syncing' && currentStatus !== 'on') {
    return;
  }
  // TODO: refactore this to move UI logic out of store to React.Context
  yield put({
    type: 'CONNECTION_UPDATE',
    payload: {
      status: status,
    },
  });
}

export function* watchConnectionStatusChange(): Generator<Effects, void, unknown> {
  const connection = {
    status: 'syncing',
    online: true,
    syncing: true,
  };
  const cardChanges = ['EDIT_CARD', 'MOVE_CARD', 'SELECTION_DELETE'];
  const authChanges = ['AUTH_FETCH'];
  const dataUpdates = ['ACCOUNT_ROADMAPS', 'ROADMAP_DATA_FETCH'];
  const changesChannel = (yield actionChannel([...cardChanges, ...authChanges])) as ActionPattern<Actions>;
  const updatesChannel = (yield actionChannel(dataUpdates)) as ActionPattern<Actions>;
  const networkChannel = getDataConnectionStatus();
  while (true) {
    const [isChange, isUpdate, isOnline]: [Actions, Actions, boolean] = (yield race([
      take(changesChannel),
      take(updatesChannel),
      take(networkChannel),
    ])) as [Actions, Actions, boolean];
    if (isChange || isUpdate) {
      connection.syncing = isChange ? true : false;
    } else if (isOnline !== undefined) {
      connection.online = isOnline;
    }
    const newStatus = ConnectionStatus[connection.online ? (connection.syncing ? 'syncing' : 'on') : 'off'];
    if (newStatus !== connection.status) {
      connection.status = newStatus;
      // TODO: replace this action with a two different sagas (network & syncing) and replace store with context
      yield put({
        type: 'CONNECTION_UPDATE',
        payload: {
          status: newStatus,
        },
      });
      if (connection.syncing) {
        yield delay(300);
      }
    }
  }
}
