import firebase from 'firebase/app';
import { eventChannel, EventChannel } from 'redux-saga';

export const getDataConnectionStatus = (): EventChannel<boolean> =>
  eventChannel((emiter) => {
    const listener = firebase.database().ref('.info/connected');
    listener.on('value', (snapshot) => {
      emiter(snapshot.val() as boolean);
    });
    return () => {
      listener.off();
    };
  });

