import C from 'constants/index';

import { subscribtion } from './subscribtion';

export const placeholderInitState = {
  started: false,
  x1: 0,
  x2: 0,
  y1: 0,
  line: 0,
  date1: '',
  done: false,
};
export const placeholderUpdate = subscribtion(placeholderInitState);

export const putPlaceholder = (e: React.MouseEvent<HTMLUListElement, MouseEvent>): void => {
  const target = e.target as HTMLLIElement;
  placeholderUpdate.update({
    started: true,
    date1: target.getAttribute('data-date') || '',
  });
};

export const movePlaceholder = (e: React.MouseEvent<HTMLUListElement, MouseEvent>): void => {
  const curTarget = e.currentTarget as HTMLUListElement;
  const target = e.target as HTMLLIElement;
  if (target.parentElement === curTarget) {
    const rect: ClientRect = curTarget.getBoundingClientRect();
    const childRect: ClientRect = target.getBoundingClientRect();
    const line = Math.floor((e.clientY - rect.top + curTarget.scrollTop - C.DAY.HEADER_HEIGHT) / C.CARD_VERTICAL_SPACE);
    if (line >= 0) {
      placeholderUpdate.update({
        x1: e.clientX - rect.left + curTarget.scrollLeft - (e.clientX - childRect.left),
        x2: e.clientX - rect.left + curTarget.scrollLeft,
        y1: C.DAY.HEADER_HEIGHT + line * C.CARD_VERTICAL_SPACE,
        date1: target.getAttribute('data-date') || '',
        line,
      });
    }
  }
};

export const removePlaceholder = (e: React.MouseEvent<HTMLElement, MouseEvent>): void => {
  if (e.type === 'mouseup') {
    placeholderUpdate.update({
      started: false,
      done: true,
    });
  } else {
    const target = e.relatedTarget as HTMLElement;
    // TODO: check if we still need `data-form` attribute
    if (!target || !target.getAttribute || target.getAttribute('data-form') !== 'edit') {
      placeholderUpdate.update({
        started: false,
      });
    }
  }
};
