import nanoid from 'utils/nanoid';

import { MainColors } from 'constants/colors';

import { AddMilestone, DeleteMilestone } from 'types/actions';

const colorNames = Object.keys(MainColors) as Array<keyof typeof MainColors>;
const getRandomColor = () => colorNames[Math.floor(Math.random() * colorNames.length)];

export const addMilestone = (date: string): AddMilestone => ({
  type: 'ADD_MILESTONE',
  payload: {
    id: nanoid(),
    date,
    text: 'Wow!',
    color: getRandomColor(),
  },
});

export const deleteMilestone = (date: string): DeleteMilestone => ({
  type: 'DELETE_MILESTONE',
  payload: {
    date,
  },
});
