import React from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

import { RoadmapNameLabel, RoadmapName } from 'styles/Header';

import { State } from 'types/state';

const RoadmapList = (): JSX.Element | null => {
  const history = useHistory();
  const roadMeta = useSelector((state: State) => state.roadMeta);
  const currentRoadmapID = useSelector((state: State) => state.ui.currentRoadmapID);
  const roadmapsData = Object.entries(roadMeta);
  if (!roadmapsData.length || !currentRoadmapID) {
    return null;
  }
  const handleChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    history.push(`/roadmap/${e.target.value}/`);
  };

  return (
    <RoadmapNameLabel>
      <RoadmapName value={currentRoadmapID} onChange={handleChange}>
        {roadmapsData.map(([key, data]) => (
          <option key={key} value={key}>
            {data.name}
          </option>
        ))}
      </RoadmapName>
    </RoadmapNameLabel>
  );
};

export default React.memo(RoadmapList);
