import React from 'react';
import { useSelector } from 'react-redux';

import LogoIcon from 'assets/Logo';
import app from 'constants/../../package.json';

import { HeaderBlock, Logo } from 'styles/Header';

import RoadmapList from './RoadmapList';
import ConnectionStatus from './ConnectionStatus';
import Avatar from './Avatar';

import { State } from 'types/state';

const Header = (): JSX.Element => {
  const isUserDataReady = useSelector((state: State) => state.user.roadmapsSet);
  return (
    <HeaderBlock>
      <Logo to="/" title={`Roadmap.Business v${app.version}`}>
        <LogoIcon />
        <span>Roadmap</span>
      </Logo>
      {isUserDataReady && <RoadmapList />}
      <ConnectionStatus />
      <Avatar />
    </HeaderBlock>
  );
};

export default React.memo(Header);
