import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { addMilestone, deleteMilestone } from 'state/actions/milestones';

import { MainColors } from 'constants/colors';

import { MilestoneBlock } from 'styles/MilestoneBlock';

import { FullDateType } from 'types';
import { State } from 'types/state';

type Props = {
  fullDate: FullDateType;
};

const Milestone = ({ fullDate }: Props): JSX.Element => {
  const milestonesObject = useSelector((state: State) => state.milestones);
  const dispatch = useDispatch();

  const milestones = Object.values(milestonesObject);
  const todayMilestones = milestones.filter((milestone) => milestone.date === fullDate.dateString);
  const todayMilestoneColor = todayMilestones[0] !== undefined ? todayMilestones[0].color : 'purple';
  const todayMilestoneStyle = todayMilestones.length ? { background: MainColors[todayMilestoneColor] } : undefined;
  const todayMilestoneNumber = todayMilestones.length ? todayMilestones.length : undefined;

  const handleClick = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    if (e.altKey || e.ctrlKey || e.metaKey || e.shiftKey) {
      return;
    }
    dispatch(addMilestone(fullDate.dateString));
  };
  const handleDoubleClick = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    if (e.altKey) {
      dispatch(deleteMilestone(fullDate.dateString));
    }
  };

  return (
    <MilestoneBlock style={todayMilestoneStyle} onClick={handleClick} onDoubleClick={handleDoubleClick}>
      {todayMilestoneNumber}
    </MilestoneBlock>
  );
};

export default React.memo(Milestone);
