import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Switch, Route, useHistory } from 'react-router-dom';

import Header from 'components/Header/Header';
import Roadmap from 'components/Screens/Roadmap';
import Overview from 'components/Screens/Overview';

import { Spinner } from 'styles/Common/Spinner';
import { GlobalStyle } from 'styles/Global';

import { State } from 'types/state';

const Routes = (): JSX.Element => (
  <Switch>
    <Route path="/roadmap/:id">
      <Roadmap />
    </Route>
    <Route path="/">
      <Overview />
    </Route>
  </Switch>
);

const Auth = (): JSX.Element => {
  const fetchig = useSelector((state: State) => state.user.fetchig);
  const userID = useSelector((state: State) => state.user.id);
  const history = useHistory();
  useEffect(() => {
    if (!fetchig && userID === '') {
      history.push('/'); // User isn't defined (logged out), but fetching has stopped
    }
  }, [history, fetchig, userID]);
  return (
    <React.Fragment>
      <GlobalStyle />
      <Header />
      {fetchig ? <Spinner /> : <Routes />}
    </React.Fragment>
  );
};

export default React.memo(Auth);
