import React from 'react';

import { DayHeaderDayBlock, DayHeaderWeekday, DayHeaderDayNumber, DayHeaderMonth } from 'styles/DayHeader';

import { FullDateType } from 'types';

type Props = {
  fullDate: FullDateType;
  monthLength: number;
};

const DayHeader = ({ fullDate, monthLength }: Props): JSX.Element => {
  return (
    <React.Fragment>
      {monthLength !== 0 && (
        <DayHeaderMonth
          dateTime={fullDate.dateString.slice(0, 7)}
          size={monthLength}
        >{`${fullDate.monthName} ${fullDate.date.year}`}</DayHeaderMonth>
      )}
      <DayHeaderDayBlock dateTime={fullDate.dateString}>
        <DayHeaderWeekday>{fullDate.weekDayShort}</DayHeaderWeekday>
        <DayHeaderDayNumber>{fullDate.date.day}</DayHeaderDayNumber>
      </DayHeaderDayBlock>
    </React.Fragment>
  );
};

export default React.memo(DayHeader);
