import React, { useRef, useState, useLayoutEffect } from 'react';
import { useDispatch } from 'react-redux';

import { getDatesArray, extendDatesArray, getDataString } from 'utils/dates';
import { getColumnSize, scrollToToday } from 'utils/ui';
import cardActions from 'utils/cardActions';
import { moveCard } from 'state/actions/cards';
import Placeholder from 'components/Placeholder/Placeholder';
import DayColumn from 'components/DateTable/DayColumn';

import C from 'constants/index';

import { DateTableBlock, TodayScroll } from 'styles/DateTable';

import { MoveCardProp } from 'types/actionsProps';
import { FullDateType } from 'types';

const columnSize: number = getColumnSize() * C.COLUMN_MULTIPLIER;

const DateTable = ({ todayTimestamp }: { todayTimestamp: number }): JSX.Element => {
  const dispatch = useDispatch();
  const listRef = useRef<HTMLUListElement>(null);
  const todayRef = useRef<HTMLLIElement>(null);
  const scroll = useRef({
    left: 0,
    fire: false,
    ready: true,
    position: 0,
  });
  const [datesArray, setDatesArray] = useState(getDatesArray(columnSize, todayTimestamp));
  const [scrollPosition, setScrollPosition] = useState(0);
  useLayoutEffect(() => {
    // This event has to fire only when the timestamp changes, but not at the initial run
    if (scroll.current.ready === false) {
      const todayString = getDataString(todayTimestamp);
      setDatesArray((currentDatesArray) =>
        currentDatesArray.map((date) => ({
          ...date,
          isToday: date.dateString === todayString,
        }))
      );
    }
  }, [todayTimestamp]);
  useLayoutEffect(() => {
    if (scroll.current.ready && listRef.current && todayRef.current) {
      scrollToToday(listRef.current, todayRef.current);
      scroll.current.position = listRef.current.scrollLeft;
      scroll.current.ready = false;
    }
    if (scroll.current.fire && listRef.current) {
      listRef.current.scrollLeft = scroll.current.left;
      scroll.current.fire = false;
    }
  }, [datesArray]);

  const remoteDispatch = (cardDetails: MoveCardProp) => dispatch(moveCard(cardDetails));
  const { pressInZone, moveInZone, releaseInZone, enterZone, leaveZone } = cardActions(remoteDispatch);

  const renderPastOrFuture = () => {
    // TODO: Optimize bloated number of DOM elements on scroll (clean memory)
    const list = listRef.current;
    if (list && list.scrollLeft !== scroll.current.position) {
      const backward = list.scrollLeft < scroll.current.position;
      const scrollLimit = list.offsetWidth * (C.COLUMN_MULTIPLIER + 1);
      if (!backward && scroll.current.position > list.scrollWidth - scrollLimit) {
        setDatesArray(extendDatesArray(datesArray, columnSize, backward));
      } else if (backward && scroll.current.position < scrollLimit) {
        scroll.current.left = list.scrollLeft + columnSize * C.DAY.WIDTH;
        scroll.current.fire = true;
        setDatesArray(extendDatesArray(datesArray, columnSize, backward));
      }
      if (listRef.current && todayRef.current) {
        const list = listRef.current;
        const today = todayRef.current;
        const todayMiddle = today.offsetLeft + today.offsetWidth / 2;
        if (todayMiddle < list.scrollLeft) {
          setScrollPosition(-1);
        } else if (todayMiddle > list.scrollLeft + list.offsetWidth) {
          setScrollPosition(1);
        } else {
          setScrollPosition(0);
        }
      }
      scroll.current.position = list.scrollLeft;
    }
  };

  const handleClick = (e: React.MouseEvent<HTMLButtonElement>): void => {
    if (listRef.current && todayRef.current) {
      scrollToToday(listRef.current, todayRef.current, true);
      e.currentTarget.blur();
    }
  };

  return (
    <React.Fragment>
      <TodayScroll scrollPosition={scrollPosition} tabIndex={scrollPosition ? undefined : -1} onClick={handleClick}>
        Today
      </TodayScroll>
      <DateTableBlock
        ref={listRef}
        onMouseDown={pressInZone}
        onMouseMove={moveInZone}
        onMouseUp={releaseInZone}
        onMouseEnter={enterZone}
        onMouseLeave={leaveZone}
        onMouseOut={leaveZone}
        onScroll={renderPastOrFuture}
      >
        {datesArray.map((fullDate: FullDateType, index: number) => (
          <DayColumn
            key={fullDate.dateString}
            fullDate={fullDate}
            index={index}
            indexFromEnd={datesArray.length - index}
            scrollRef={fullDate.isToday ? todayRef : undefined}
          />
        ))}
        <Placeholder />
      </DateTableBlock>
    </React.Fragment>
  );
};

export default React.memo(DateTable);
