import { MainColors } from 'constants/colors';

export type Card = {
  id: string;
  line: number;
  text: string;
  from: string;
  until: string;
  progress?: number;
};

export type Milestone = {
  id: string;
  text: string;
  date: string;
  from?: never;
  until?: never;
  progress?: never;
  color: keyof typeof MainColors;
};

export enum ElementType {
  card = 'card',
  milestone = 'milestone',
  none = '',
}

export type List<Type> = {
  [key: string]: Type;
};

export interface PersistentState {
  cards: List<Card>;
  milestones: List<Milestone>;
}

export type SelectedElement = Card | Milestone | undefined;

export enum ConnectionStatus {
  on = 'on',
  off = 'off',
  syncing = 'syncing',
}

export type RoadMeta = {
  name: string;
  createdAt: number;
};

export type EditSelection = Card;

export interface Selection extends EditSelection {
  isNew: boolean;
  type: ElementType;
}

export interface State extends PersistentState {
  selection: Selection;
  user: {
    id: string;
    name: string;
    email: string;
    photoURL: string;
    fetchig: boolean;
    roadmaps: {
      [key: string]: boolean;
    };
    roadmapsSet: boolean;
  };
  roadMeta: {
    [key: string]: RoadMeta;
  };
  ui: {
    connectionStatus: ConnectionStatus;
    currentRoadmapID: string;
  };
}
