import React from 'react';
import { useSelector } from 'react-redux';

import { getDaysInterval, getDaysInMonth } from 'utils/dates';
import { removePlaceholder } from 'utils/placeholderActions';

import C from 'constants/index';

import { DayHeaderBlock } from 'styles/DayHeader';
import { DateColumnItem } from 'styles/DateTable';

import Card from './Card';
import Milestone from './Milestone';
import DayHeader from './DayHeader';

import { FullDateType } from 'types';
import { State } from 'types/state';

type Props = {
  fullDate: FullDateType;
  index: number;
  indexFromEnd: number;
  scrollRef?: React.RefObject<HTMLLIElement>;
};

const DateColumn = ({ fullDate, index, indexFromEnd, scrollRef }: Props): JSX.Element => {
  const cardsObject = useSelector((state: State) => state.cards);
  const cards = Object.values(cardsObject);

  const today = fullDate.isToday ? 'today' : '';
  const month = fullDate.date.day === 1 ? 'month' : '';
  const weekend = fullDate.isWeekend ? 'weekend' : '';
  const classAddition = (today + ' ' + weekend + ' ' + month).trim();

  const monthLength =
    index === 0 || fullDate.date.day === 1
      ? index === 0
        ? getDaysInMonth(fullDate.date.year, fullDate.date.month) - fullDate.date.day + 1
        : Math.min(getDaysInMonth(fullDate.date.year, fullDate.date.month), indexFromEnd)
      : 0;

  return (
    <DateColumnItem className={classAddition} data-date={fullDate.dateString} ref={scrollRef ? scrollRef : undefined}>
      <DayHeaderBlock onMouseEnter={removePlaceholder}>
        <DayHeader fullDate={fullDate} monthLength={monthLength} />
        <Milestone fullDate={fullDate} />
      </DayHeaderBlock>
      {cards.map((card) => {
        if (card.from === fullDate.dateString) {
          const dayDiff = getDaysInterval(card.from, card.until);
          const style = {
            width: `${dayDiff * C.DAY.WIDTH - C.CARD_HORIZONTAL_SPACE}px`,
            top: `${C.DAY.HEADER_HEIGHT + card.line * C.CARD_VERTICAL_SPACE}px`,
          };
          return <Card key={card.id} card={card} style={style} />;
        }
        return null;
      })}
    </DateColumnItem>
  );
};

export default React.memo(DateColumn);
